<template>
    <div></div>
</template>

<script>
import rolling from '@/common/es/api_bonus/doing/_rolling_tree'
import computed from '@/common/es/api_bonus/doing/_computed'
import vip from '@/common/es/api_bonus/doing/_vip'

import recommend from '../../common/es/api_bonus/_bonus/_recomend'
import team_layer from '../../common/es/api_bonus/_bonus/_team_layer'
import active_house from '../../common/es/api_bonus/_bonus/_active_house'

    export default {
        props: {

        },
        name: '',
        data() {
            return {
                chrou: '',
                transfer: [ ],
                unit_users_sponser: [ ],
                unit_users_enroller: [ ],
            }
        },
        methods: {
            
            async chronuLoading() {
                let res

                let condition = {
                    '_where[start_lte]': this.view.getToday(),
                    '_where[end_gte]': this.view.getToday()
                }

                res = await this.conn.get(
                    this.api.chronus,
                    this.$store.state.token,
                    condition
                )

                console.log('INIT 搜索 时光 =', res)
                if (res) {
                    if (res.length > 0) {
                        condition = true 
                    }
                }

                if (condition === true) {
                    res[1] = true
                    this.$store.commit('saveChronus', res)
                    this.chrou = res[0]

                    return true
                } else {
                    
                    // alert('新的结算日程开始了，但是数据库未有关于新日程的时间准备，请移步后台新增时间代号。')
                }

                return false
            },
            async buyLoading() {
                const res = await this.conn.get(
                    this.api.buys,
                    this.$store.state.token,
                    {
                        'chronu': this.chrou.id
                    }
                )

                return res.map(e => {
                        return {
                            num: e.num,
                            id: e.id,
                            pv: e.product.pv,
                            first_buy: e.first_buy,
                            users_permissions_user: e.users_permissions_user.id,
                            buy_time: e.buy_time,
                            product: e.product.id,
                        }
                    })
            },
            async userLoading(mode = 'SPONSER') {
                const condition = { }
                if (mode == 'SPONSER') {
                    condition['_where[relation_index_sponser_contains]'] = this.$store.state.user_backend.relation_index_sponser
                } else {
                    condition['_where[relation_index_enroller_contains]'] = this.$store.state.user_backend.relation_index_enroller
                }

                const res = await this.conn.get(
                    this.api.users,
                    this.$store.state.token,
                    condition
                )
                
                return res
            },
            rebuild_user(user) {
                delete user.created_at
                delete user.updated_at
                delete user.role
                delete user.provider
                delete user.confirmed
                delete user.blocked
                delete user.email
            },
            async init() {
                
                let finished = false
                let res_sponser, res_enroller
                const _user_bc = this.$store.state.user_backend

                if (this.$store.state.chronus && _user_bc) {
                    
                    const _init_s = _user_bc.sponser_id
                    const _init_e = _user_bc.enroller_id

                    this.chrou = this.$store.state.chronus
                    
                    console.log('INIT 资料 =', this.chrou.mark, _user_bc.username)
                    
                    let users_s = await this.userLoading('SPONSER')
                    let users_e = await this.userLoading('ENROLLER')
                    let buys = await this.buyLoading()

                    // 
                    users_s.map(user => {
                        let res = []
                            buys.map(buy => { if (user.id == buy.users_permissions_user) { res.push(buy) } })
                            user.buys = res
                        return user
                    })
                    users_e.map(user => {
                        let res = []
                            buys.map(buy => { if (user.id == buy.users_permissions_user) { res.push(buy) } })
                            user.buys = res
                        return user
                    })

                    // 建立树状图
                    res_sponser = await rolling(users_s, 'SPONSER', this.rebuild_user, _init_s)
                    res_enroller = await rolling(users_e, 'ENROLLER', this.rebuild_user, _init_e)

                    // 获取 中间数组， 获取 VIP 需要的参数
                    this.transfer = await computed(res_sponser, 'SPONSER', this.transfer, null)
                    this.transfer = await computed(res_enroller, 'ENROLLER', this.transfer, null)

                    // 拿到 VIP
                    this.transfer.map(e => {

                        e.vip = vip(e)
                        return e
                    })

                    this.unit_users_sponser = res_sponser
                    this.unit_users_enroller = res_enroller

                    // 计算 
                    await recommend(res_enroller, this.transfer)
                    await team_layer(res_sponser, this.transfer)
                    await active_house(res_sponser, this.transfer)

                    this.bonus._build_res(res_sponser, this.transfer, function (u, t) {

                        for(let i= 0; i< t.length; i++ ) {

                            if (u.unid == t[i].unid) {
                                u.transfer = t[i]
                                t[i].bonus = u.bonus
                            }
                        }
                    })

                    this.bonus._build_res(res_enroller, this.transfer, function (u, t) {

                        for(let i= 0; i< t.length; i++ ) {
                            if (u.unid == t[i].unid) {
                                u.transfer = t[i]
                                t[i].bonus.usd_recommend = u.bonus.usd_recommend
                            }
                        }
                    })

                    console.log('sponser =', this.unit_users_sponser, 'enroller =', this.unit_users_enroller)

                    // Saving
                    this.$store.commit('saveUserSponser', this.unit_users_sponser)
                    this.$store.commit('saveUserEnroller', this.unit_users_enroller)
                    this.$store.commit('saveTransfer', this.transfer)

                    finished = true
                }

                return finished
            },
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


