<template>
    <qiong-loading></qiong-loading>
</template>

<script>
import QiongLoading from '../Qiong/Ui/QiongLoading.vue'
    export default {
  components: { QiongLoading },
        name: '',
        data() {
            return {
                
            }
        },
        async mounted() {
            if (this.$store.state.token) {
                const chronu = await this.chronusNow()
                this.$store.commit('saveChronus', [ chronu, true ])
            }
        },
        methods: {
            
            async chronusNow() {

                let res

                let condition = {
                    '_where[start_lte]': this.view.getToday(),
                    '_where[end_gte]': this.view.getToday()
                }

                res = await this.conn.get(
                    this.api.chronus,
                    this.$store.state.token,
                    condition
                )

                console.log('搜索 時光 =', res)
                if (res) {
                    if (res.length > 0) {
                        return res[0] 
                    }
                }

                this.view.alert_message('新的結算日程開始了，但是數據庫未有關於新日程的時間準備，請移步後臺新增時間代號。')
                return ''
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


