import util from '../util'
import conf from '../conf'

const team_layer = function(ipv ) {

    let res = 0
    if (ipv > conf.USER_PV_LIMIT) {
        res = 1
    }
    
    return [
        '无', '有'
    ] [ res ]
}

const order_role = function(is_origin) {

    return 'Member'
    if (is_origin) {
        return 'Member'
    }

    return 'Retail Customer'
}

export default {
    team_layer,
    order_role
}


