<template>
    
    <v-container fluid class="bg-hui h-100">
        <v-row>
            <v-col cols="12"> <div class="title pl-4 pt-2"> &nbsp; </div> </v-col>

            <!-- v-col cols="12">

                <carousel class="w-full" :per-page="3" :mouse-drag="false" :paginationActiveColor="'#ffffff'">
                    <slide>
                        Slide 1 Content
                    </slide>
                    <slide>
                        Slide 2 Content
                    </slide>
                    <slide>
                        Slide 2 Content
                    </slide>
                    <slide>
                        Slide 2 Content
                    </slide>
                </carousel>
            </v-col -->
        </v-row>

        <v-row class="w-full">
            <v-col col="2"></v-col>
            <v-col col="8" class="pt-1">

                <div class="qiong-txt-18 qiong-txt-primary-dark">
                    結算月份
                </div>
                <div class="pb-2">
                    
                    <div v-for="(c, i) in chronus" :key="i" class="my-4">
                        <chronus-item :chronu="c"></chronus-item>
                        <v-divider v-if="i == c.length - 1"></v-divider>
                    </div>

                </div>
            </v-col>
            <v-col col="2"></v-col>
        </v-row>

        <v-row>
            <v-col class="text-right">
                
            </v-col>
        </v-row>
    </v-container>

</template>

<script>
import ChronusItem from './ChronusItem.vue'
    export default {
  components: { ChronusItem },
        name: '',
        data() {
            return {
                chronus: [ ]
            }
        },
        mounted() {
            this.chronusLoading()
        },
        methods: {
            async chronusLoading() {
                const res = await this.conn.get(
                    this.api.chronus,
                    this.$store.state.token,
                    {
                        '_sort': 'published_at:DESC',
                    }
                )

                if (res) {
                    this.chronus = res
                }
            },
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


