import util from './util'
import conf from './conf'

import computed from './doing/_computed'
import rolling from './doing/_rolling_tree'
import vip from './doing/_vip'

import vip_view from './_view/_vip_view'
import has from './_view/_has'

import recommend from './_bonus/_recomend'
import team_layer from './_bonus/_team_layer'
import active_house from './_bonus/_active_house'

const _build_newer = function(items, transfer, hooking) {
    let res = [ ]

    items.map(e => {

        let c = [ ]
        hooking(e, transfer)

        if (util.judge_has_children(e)) {
            c = _build_res(e.children, transfer, hooking)
            e.children = c
        }

        res.push(e)
    })

    return res
}

const _build_res = function(items, transfer, hooking) {

    items.map(e => {

        hooking(e, transfer)

        if (util.judge_has_children(e)) {
            _build_res(e.children, transfer, hooking)
        }
    })
}


const _build = function(items, transfer, hooking) {

    items.map(async e => {

        if (util.judge_has_children(e)) {

            _build(e.children, transfer, hooking)
        }

        await hooking(e, transfer)
    })
}

export default {
    _build,
    _build_res,
    _build_newer,
    
    rolling,
    computed,
    vip,
    
    has,
    conf,
    vip_view,

    recommend,
    team_layer,
    active_house,
}



