
const Bonus = () => import('@/pages/Bonu/Bonus')

const BonusRecommend = () => import('@/pages/Bonu/Recommend/BonusRecommend')
const BonusTeamLayer = () => import('@/pages/Bonu/TeamLayer/BonusTeamLayer')
const BonusActiveHouse = () => import('@/pages/Bonu/ActiveHouse/BonusActiveHouse')

const BonusRecommendOne = () => import('@/pages/Bonu/Recommend/RecommendOne/BonusRecommendOne')
const BonusActiveHousOne = () => import('@/pages/Bonu/ActiveHouse/ActiveHouseOne/ActiveHouseOne')
const BonusTeamLayerOne = () => import('@/pages/Bonu/TeamLayer/TeamLayerOne/BonusTeamLayerOne')


export default {
    
    path: '/home/bonus',
    component: Bonus,
    
    redirect:'/home/bonus/active_house',
    children: [
        {
            path: '/home/bonus/active_house',
            component: BonusActiveHouse,
        },
        {
            path: '/home/bonus/active_house_one',
            component: BonusActiveHousOne
        },

        {
            path: '/home/bonus/recommend',
            component: BonusRecommend,
        },
        {
            path: '/home/bonus/recommend_one',
            component: BonusRecommendOne
        },

        {
            path: '/home/bonus/active_layer',
            component: BonusTeamLayer,
        },
        {
            path: '/home/bonus/active_layer_one',
            component: BonusTeamLayerOne,
        },
    ]
}


